<template>
  <el-dialog custom-class="signup-dialog" title="회원가입" v-model="state.dialogVisible" @close="handleClose">
    <el-form :model="state.form" :rules="state.rules" ref="signupForm" :label-position="state.form.align">

      <el-form-item prop="belong" label="소속" :label-width="state.formLabelWidth" >
        <el-input v-model="state.form.belong" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item prop="position" label="직책" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.position" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item prop="name" label="이름" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.name" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item prop="id" label="아이디" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.id" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item prop="password" label="패스워드" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.password" autocomplete="off" show-password></el-input>
      </el-form-item>
      <el-form-item prop="password_repeat" label="패스워드 확인" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.password_repeat" autocomplete="off" show-password></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="prmiary" @click="clickSignup">회원가입</el-button>
      </span>
    </template>
  </el-dialog>
</template>
<style>
.signup-dialog {
  width: 400px !important;
  height: 550px;
}
.signup-dialog .el-dialog__headerbtn {
  float: right;
}
.signup-dialog .el-form-item__content {
  margin-left: 0 !important;
  float: right;
  width: 200px;
  display: inline-block;
}
.signup-dialog .el-form-item {
  margin-bottom: 20px;
}
.signup-dialog .el-form-item__error {
  font-size: 12px;
  color: red;
}
.signup-dialog .el-input__suffix {
  display: none;
}
.signup-dialog .el-dialog__footer {
  margin: 0 calc(50% - 80px);
  padding-top: 0;
  display: inline-block;
}
.signup-dialog .dialog-footer .el-button {
  width: 120px;
}
</style>
<script>
import { reactive, computed, ref, onMounted } from 'vue'
import { useStore } from 'vuex'
import axios from 'axios'

export default {
  name: 'signup-dialog',

  props: {
    open: {
      type: Boolean,
      default: false
    }
  },

  setup(props, { emit }) {
    const store = useStore()
    // 마운드 이후 바인딩 될 예정 - 컨텍스트에 노출시켜야함. <return>
    const signupForm = ref(null)

    /*
      // Element UI Validator
      // rules의 객체 키 값과 form의 객체 키 값이 같아야 매칭되어 적용됨
      //
    */
    const state = reactive({
      form: {
        belong: '',
        position: '',
        name: '',
        id: '',
        password: '',
        password_repeat: '',
        align: 'left'
      },
      rules: {
        belong: [
          { max: 30, message: '최대 30자까지 가능합니다.'}
        ],
        position:[
          { max:30, message: '최대 30자까지 가능합니다.'}
        ],
        name: [
          { required: true, message: '필수입력 항목입니다.', trigger: 'blur' },
          { max: 30, message: '최대 30자까지 가능합니다.'}
        ],
        id: [
          { required: true, message: '필수입력 항목입니다.', trigger: 'blur' },
          { max: 16, message: '최대 16자까지 가능합니다.'}
        ],
        password: [
          { required: true, message: '필수입력 항목입니다.', trigger: 'blur' },
          { max: 16, message: '최대 16자까지 가능합니다.'},
          { min: 9, message: '최소 9자를 입력해야 합니다.'}
        ],
        password_repeat: [
          { required: true, message: 'Please input password confirm', trigger: 'blur' }
        ],
      },
      dialogVisible: computed(() => props.open),
      formLabelWidth: '120px'
    })

    onMounted(() => {
      // console.log(loginForm.value)
    })

    const clickSignup = function () {
      // 회원가입 클릭 시 validate 체크 후 그 결과 값에 따라, 로그인 API 호출 또는 경고창 표시
      signupForm.value.validate((valid) => {
        if (valid) {
          console.log('submit')
          store.dispatch('root/requestSignup', { userId: state.form.id,
            password: state.form.password,
            position: state.form.position,
            department: state.form.belong,
            name: state.form.name })
          .then(function (result) {
            alert('회원가입완료');
            handleClose();
            //로그인상태로 되면 좋을듯
          })
          .catch(function (err) {
            alert(err)
          })
        }
        else {
          alert('Validate error!')
        }
      });
    }

    const handleClose = function () {
      state.form.id = ''
      state.form.password = ''
      state.form.belong= ''
      state.form.position= ''
      state.form.password_repeat= ''
      state.form.name= ''
      emit('closeSignupDialog')
    }

    return { state, handleClose, signupForm, clickSignup }
  }
}
</script>
